.progress-bar {
    &__row {
        display: flex;
        justify-content: space-between; } }

.grid-item {
    margin-bottom: 30px; }

.grid-item,
.grid-sizer {
    width: 32%; }

.gutter-sizer {
    width: 2%; }



.card {
    background-color: #fff;
    box-shadow: 0px 3px 6px 0px rgba(0, 11, 10, 0.05);
    &__header {
        padding-bottom: 1rem;
        padding-top: 2rem; }
    &__header-text {
        display: inline-block;
        width: 72%;
        padding: 0 1.875rem;
        background-color: #faac03;
        color: #fff;
        font-size: .875rem;
        font-weight: 700;
        text-transform: uppercase; }
    &__body {
        padding: 1.5rem 1.875rem;
        padding-top: 0; }
    &__body-title {
        color: #333333;
        font-weight: 700;
        font-size: 1.375rem;
        text-transform: uppercase;
        margin-bottom: 1rem; }
    &__body-text {
        color: #868686;
        font-weight: 300;
        font-size: 1rem; } }
